* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
 
  
  body {
    font-size: 16px;
    font-family: 'cairo', sans-serif;
    line-height: 1.7;
    font-weight: bold;
    
     
       

    
  }
  .res-img {
    width: 100%;
  }
  
  .container {
    max-width: 1370px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;
  }
  
  .active {
    color: $secondary_color !important;
  }
  
  %limited_width {
    max-width: 580px;
    margin: 0 auto;
    text-align: center;
    font-size: 30px;
    padding: 20px 0px;
    color: $secondary_color;
  }
  
  %btn {
    padding: 15px;
    color: $white_color;
    border-radius: 10px;
    text-align: center;
  }
  
  .title {
    margin: 0 auto;
    max-width: 580px;
    text-align: center;
    font-size: 30px;
    padding: 20px 0px;
    color: $secondary_color;
  }
